import React, {Component} from 'react';
import ViewPortfolio from '../viewing/ViewPortfolio';

class Portfolio extends Component {
    state = {
        portfolio: [
            {id: 1, title: "To-do App", imageUrl: "https://i.ibb.co/7K3Fhgz/todoapp-home-page-crop.png", url: "https://ziadhishamali.github.io/todoapp"},
            {id: 2, title: "Book a Book", imageUrl: "https://i.ibb.co/0D1bmkT/home-page-crop.png", url: "https://ziadhishamali.github.io/bookabook"},
            {id: 3, title: "Paint", imageUrl: "https://i.ibb.co/Wx86Vvw/paint-home-page-crop.png", url: "https://github.com/ziadhishamali/paint"}
        ],
    }

    render() {
        return (
            <div className="portfolio-container box-shadow margin-top">
                <h1 className="header white-text medium-text berlin margin-left">PORTFOLIO<span className="orange-text">.</span></h1>
                <ViewPortfolio portfolio={this.state.portfolio}/>
            </div>
        );
    }
}

export default Portfolio