import React from 'react';
import Indicator from './Indicator';

const EachSkill = ({skill}) => {
    const showSkills = skill.length ? (
        skill.map(s => {
            return (
                <div key={s.id} className="container-wrapper align no-margin no-padding margin-left-2">
                    <span className="header white-text small-text">{s.name}</span>
                    <Indicator level={s.level}/>
                </div>
            );
        })
    ) : (
        <span></span>
    )
    return (
        <div className="container-wrapper no-border">
            {showSkills}
        </div>
    );
}

export default EachSkill