import React from 'react';

const ViewPortfolio = ({portfolio}) => {
    const showPortfolio = portfolio.length ? (
        portfolio.map(p => {
            return (
                <div key={p.id} className="portfolio-container white-background box-shadow-2 margin-left margin-right margin-bottom">
                    <a href={p.url} target="_blank">
                        <h1 className="header black-text align-center small-text margin-left margin-right">{p.title}</h1>
                    </a>
                    <img className="portfolio-image" src={p.imageUrl} alt={p.title}/>
                </div>
            );
        })
    ) : (
        <p>Loading Portfolio ...</p>
    );
    return (
        <div className="container-wrapper no-background no-border">
            {showPortfolio}
        </div>
    );
}

export default ViewPortfolio