import React from 'react';
import EachSkill from './EachSkill';

const ViewSkills = ({skills}) => {
    const showSkills = skills.length ? (
        skills.map(skill => {
            return (
                <div key={skill.id} className="skills-container no-background no-border">
                    <h1 className="header black-text small-text margin-left">{skill.title}</h1>
                    <EachSkill skill={skill.all}/>
                </div>
            );
        })
    ) : (
        <p>Loading skills ...</p>
    );
    return (
        <div className="skills-container box-shadow margin-top">
            <h1 className="header white-text medium-text berlin margin-left">SKILLS<span className="orange-text">.</span></h1>
            {showSkills}
        </div>
    );
}

export default ViewSkills