import React, {Component} from 'react';
import ViewSkills from '../viewing/ViewSkills';
import '../../styles/Skills.css';
import Portfolio from '../layout/Portfolio';

class Skills extends Component {
    state = {
        skills: [
            {id: 1, title: "Programming Languages", all: [{id: 1, name: "Java", level: 8}, {id: 2, name: "JavaScript", level: 7}, {id: 3, name: "C,C++", level: 7}, {id: 4, name: "Python", level: 6}, {id: 5, name: "Ruby", level: 4}]},
            {id: 2, title: "Web Development", all: [{id: 6, name: "JavaScript", level: 7}, {id: 7, name: "React.js", level: 7}, {id: 8, name: "Node.js", level: 5}, {id: 9, name: "Django", level: 5}, {id: 10, name: "Express.js", level: 4}]}
        ],
    }

    render() {
        return (
            <div className="section justify align auto-height parallax bg2">
                <Portfolio/>
                <ViewSkills skills={this.state.skills}/>
            </div>
        );
    }
}

export default Skills